import React, { useEffect, useState } from "react";

import { Link } from "react-router-dom";
import { useDispatch, useSelector } from "react-redux";
import {
  getAllAuthorsAsync,
  selectAllAuthors,
} from "../slices/author/authorSlice";

const Authors = () => {
  const dispatch = useDispatch();
  const authordata = useSelector(selectAllAuthors);
  const [loading, setLoading] = useState(true); // State to track loading
  useEffect(() => {
    dispatch(getAllAuthorsAsync())
      .then(() => setLoading(false)) // Set loading to false when data is fetched
      .catch(() => setLoading(false)); // Handle error case
  }, []);

  const Author = authordata
    ? authordata.map(({ _id, avatar, name, posts }) => {
        return (
          <>
            <div className="max-w-md p-5 sm:flex sm:space-x-2 h-28 dark:bg-gray-900 dark:text-gray-100">
              <Link key={_id} to={`/post/users/${_id}`}>
                <div className="flex-shrink-0 w-full mb-6 sm:h-32 sm  :w-32 sm:mb-0">
                  <img
                    src={`http://localhost:8080/uploads/${avatar}`}
                    alt=""
                    className="object-cover object-center w-full h-20 rounded dark:bg-gray-500 img_author"
                  />
                </div>
                <div className="flex flex-col space-y-2">
                  <div>
                    <h2 className="text-2xl font-semibold">{name}</h2>
                    <span className="text-sm dark:text-gray-400">
                      {posts} posts
                    </span>
                  </div>
                </div>
              </Link>
            </div>
          </>
        );
      })
    : "";
  return (
    <>
      <div className="p-5 flex flex-wrap gap-5 author_class">
        {loading ? ( // Render loader if loading state is true
          <div>Loading...</div>
        ) : (
          Author // Render articles once loading is complete
        )}
      </div>
    </>
  );
};

export default Authors;
