import React, { useState } from "react";
import { useDispatch, useSelector } from "react-redux";
import { Link, Navigate } from "react-router-dom";
import { selectLoggedInUser } from "../slices/auth/authSlice";
import {
  changeUserAvatarAsync,
  changeUserDetailAsync,
  selectError,
  selectStatus,
} from "../slices/author/authorSlice";

const UserProfile = () => {
  const user = useSelector(selectLoggedInUser);
  const status = useSelector(selectStatus);
  const err = useSelector(selectError);
  const [error, setError] = useState(err);
  const dispatch = useDispatch();
  const [editing, setEditing] = useState(false);
  const [formData, setFormData] = useState({
    name: user.name,
    email: user.email,
    avatar: null,
  });

  const handleEditToggle = () => {
    setEditing(!editing);
  };

  const handleChange = (e) => {
    const { name, value } = e.target;
    setFormData((prevData) => ({
      ...prevData,
      [name]: value,
    }));
  };

  const handleAvatarChange = (e) => {
    setFormData({ ...formData, avatar: e.target.files[0] });
  };

  const handleSubmitAvatar = (e) => {
    e.preventDefault();
    if (formData.avatar) {
      dispatch(changeUserAvatarAsync(formData.avatar));
    } else {
      setError("Please select an avatar image.");
      setTimeout(() => {
        setError("");
      }, 3000);
    }
  };

  const handleSubmitDetails = (e) => {
    e.preventDefault();
    if (!(formData.name && formData.email)) {
      setError(" Name and email fields cannot be empty.");
      setTimeout(() => {
        setError("");
      }, 3000);
    } else if (user.name !== formData.name || user.email !== formData.email) {
      dispatch(
        changeUserDetailAsync({ name: formData.name, email: formData.email })
      );
    } else if (user.name === formData.name && user.email === formData.email) {
      setError(" old name or email fields is same as new name or email");
      setTimeout(() => {
        setError("");
      }, 3000);
    }
  };

  return (
    <>
      {error && (
        <div
          className="bg-red-100 border mt-5 border-red-400 text-red-700 px-4 py-3 rounded relative"
          role="alert"
        >
          <strong className="font-bold">Something went wrong!</strong>
          <span className="block sm:inline">{error}</span>
          <span
            className="absolute top-0 bottom-0 right-0 px-4 py-3 cursor-pointer"
            onClick={() => {
              setError("");
            }}
          >
            <svg
              className="fill-current h-6 w-6 text-red-500"
              xmlns="http://www.w3.org/2000/svg"
              viewBox="0 0 20 20"
            >
              <title>Close</title>
              <path
                fillRule="evenodd"
                d="M14.348 14.849a1.2 1.2 0 0 1-1.697 0L10 11.819l-2.651 3.029a1.2 1.2 0 1 1-1.697-1.697l2.758-3.15-2.759-3.152a1.2 1.2 0 1 1 1.697-1.697L10 8.183l2.651-3.031a1.2 1.2 0 1 1 1.697 1.697l-2.758 3.152 2.758 3.15a1.2 1.2 0 0 1 0 1.698z"
              />
            </svg>
          </span>
        </div>
      )}
      {user ? (
        <div className="bg-gray-100 min-h-screen">
          <div className="container mx-auto p-8">
            <div className="bg-white rounded-lg shadow-md p-8">
              <div className="flex items-center mb-6">
                <img
                  src={`http://localhost:8080/uploads/${user.avatar}`}
                  alt="User Avatar"
                  className="w-16 h-16 rounded-full mr-4"
                />
                <Link to={`/myposts/ds`}>
                  <div>
                    <h1 className="text-2xl font-bold">{user.name}</h1>
                    <p className="text-gray-500">{user.email}</p>
                  </div>
                </Link>
              </div>
              <div className="mb-4">
                <button
                  onClick={handleEditToggle}
                  className="bg-gray-500 text-white px-4 py-2 rounded-md hover:bg-gray-600"
                >
                  {editing ? "Cancel Edit" : "Edit Profile"}
                </button>
              </div>
              {editing && (
                <>
                  <form onSubmit={handleSubmitAvatar}>
                    <div className="mb-4">
                      <label
                        htmlFor="avatar"
                        className="block text-sm font-medium text-gray-600 mb-2"
                      >
                        Avatar
                      </label>
                      <input
                        type="file"
                        id="avatar"
                        name="avatar"
                        onChange={handleAvatarChange}
                        accept="jpeg,png,jpg"
                        className="border rounded-md w-full py-2 px-3"
                      />
                    </div>
                    <button
                      type="submit"
                      className="bg-blue-500 text-white px-4 py-2 rounded-md hover:bg-blue-600"
                    >
                      Change Avatar
                    </button>
                  </form>
                  <form>
                    <div className="mb-4 w-96">
                      <label
                        htmlFor="name"
                        className="block text-sm font-medium text-gray-600 mb-2"
                      >
                        Name
                      </label>
                      <input
                        type="text"
                        id="name"
                        name="name"
                        value={formData.name}
                        onChange={handleChange}
                        className="border rounded-md w-full py-2 px-3"
                      />
                    </div>
                    <div className="mb-4">
                      <label
                        htmlFor="email"
                        className="block text-sm font-medium text-gray-600 mb-2"
                      >
                        Email
                      </label>
                      <input
                        type="email"
                        id="email"
                        name="email"
                        value={formData.email}
                        onChange={handleChange}
                        className="border rounded-md w-full py-2 px-3"
                      />
                    </div>
                    <button
                      type="submit"
                      className="bg-blue-500 text-white px-4 py-2 rounded-md hover:bg-blue-600"
                      onClick={handleSubmitDetails}
                    >
                      Update Details
                    </button>
                  </form>
                </>
              )}
            </div>
          </div>
        </div>
      ) : (
        <Navigate to="/login" replace={true}></Navigate>
      )}
    </>
  );
};

export default UserProfile;
