import { createAsyncThunk, createSlice } from "@reduxjs/toolkit";
import { changeUserAvatar, changeUserDetail, getAllAuthors } from "./authorApi";

const initialState = {
  authors: null,
  status: "idle",
  error: null,
  author: null,
};

export const getAllAuthorsAsync = createAsyncThunk(
  "author/getAllAuthors",
  async () => {
    const response = await getAllAuthors(); // The value we return becomes the `fulfilled` action payload
    return response.data;
  }
);
export const changeUserAvatarAsync = createAsyncThunk(
  "author/changeUserAvatar",
  async (avatar) => {
    const response = await changeUserAvatar(avatar); // The value we return becomes the `fulfilled` action payload
    return response;
  }
);
export const changeUserDetailAsync = createAsyncThunk(
  "author/changeUserDetail",
  async (userdata) => {
    const response = await changeUserDetail(userdata);
    console.log("R" + response); // The value we return becomes the `fulfilled` action payload
    return response;
  }
);
const postSlice = createSlice({
  name: "author",
  initialState,
  reducers: {},
  extraReducers: (builder) => {
    builder
      .addCase(getAllAuthorsAsync.pending, (state) => {
        state.authors = null;
        state.status = "loading";
      })
      .addCase(getAllAuthorsAsync.fulfilled, (state, action) => {
        state.authors = action.payload;
        state.status = "success";
        state.author = null;
      })
      .addCase(getAllAuthorsAsync.rejected, (state, action) => {
        state.error = action.payload;
        state.status = "failed";
      })
      .addCase(changeUserAvatarAsync.pending, (state) => {
        state.status = "loading";
      })
      .addCase(changeUserAvatarAsync.fulfilled, (state, action) => {
        let data;
        console.log(action.payload);
        const userInfoString = localStorage.getItem("userInfo");

        if (userInfoString) {
          const userInfo = JSON.parse(userInfoString);

          const name = userInfo.name;
          const email = userInfo.email;
          const avatar = action.payload.avatar;

          data = {
            name,
            email,
            avatar,
          };
        }

        localStorage.setItem("userInfo", JSON.stringify(data));
        state.status = "success";
        // Reload the page
        window.location.reload();
      })
      .addCase(changeUserAvatarAsync.rejected, (state, action) => {
        state.error = action.payload;
        state.status = "failed";
      })
      .addCase(changeUserDetailAsync.pending, (state) => {
        state.status = "loading";
      })
      .addCase(changeUserDetailAsync.fulfilled, (state, action) => {
        let data;
        console.log(action.payload);
        const userInfoString = localStorage.getItem("userInfo");

        if (userInfoString) {
          const userInfo = JSON.parse(userInfoString);

          const name = action.payload.name;
          const email = action.payload.email;
          const avatar = userInfo.avatar;

          data = {
            name,
            email,
            avatar,
          };
        }

        localStorage.setItem("userInfo", JSON.stringify(data));
        state.status = "success";
        // Reload the page
        window.location.reload();
      })
      .addCase(changeUserDetailAsync.rejected, (state, action) => {
        state.error = action.payload;
        state.status = "failed";
      });
  },
});

export const selectAllAuthors = (state) => state.author.authors;

export const selectAuthor = (state) => state.author.author;

export const selectError = (state) => state.author.error;

export const selectStatus = (state) => state.author.status;

export default postSlice.reducer;
