import React, { useState } from "react";
import { useDispatch } from "react-redux";
import { Link, useNavigate } from "react-router-dom";
import { loginUserAsync } from "../slices/auth/authSlice";

const Login = () => {
  const dispatch = useDispatch();
  const navigate = useNavigate();
  const [userData, setUserData] = useState({
    email: "",
    password: "",
  });
  const [err, setErr] = useState(null);
  const [validationErrors, setValidationErrors] = useState({});
  
  const handleChange = (e) => {
    const { name, value } = e.target;
    setUserData((prevData) => ({
      ...prevData,
      [name]: value,
    }));
  };

  const validateForm = () => {
    let errors = {};
    if (!userData.email) {
      errors.email = "Email is required";
    }
    if (!userData.password) {
      errors.password = "Password is required";
    }
    setValidationErrors(errors);
    return Object.keys(errors).length === 0;
  };

  const handleLogin = (e) => {
    e.preventDefault();
    if (!validateForm()) {
      return;
    }
  
    dispatch(loginUserAsync(userData))
      .then((response) => {
        if (response.error) {
          const errorMessage = JSON.parse(response.error.message).message;
          setErr(errorMessage);
        } else {
          setErr(null); // Clear error only on successful login
          setUserData({
            email: "",
            password: "",
          });
          navigate("/");
        }
      })
      .catch((error) => {
        console.error("Error occurred while logging in:", error);
        setErr("An error occurred during login.");
      });
  };
  

  return (
    <>
      <div className="min-h-screen bg-gray-100  flex flex-col justify-center sm:py-12">
        <div className="relative py-3 sm:max-w-xl sm:mx-auto">
          <div className="absolute inset-0 bg-gradient-to-r from-blue-300 to-blue-600 shadow-lg transform -skew-y-6 sm:skew-y-0 sm:-rotate-6 sm:rounded-3xl"></div>
          <div className="relative px-4 py-10 bg-white shadow-lg sm:rounded-3xl sm:p-20">
            <div className="max-w-md mx-auto">
              <div>
                <h1 className="text-2xl font-semibold">Login Up Form</h1>
              </div>
              <div className="divide-y divide-gray-200">
                <div className="py-8 text-base leading-6 space-y-4 text-gray-700 sm:text-lg sm:leading-7">
                  {err && (
                    <div className="text-red-600">{err}</div>
                  )}
                  <div className="relative">
                    <input
                      id="email"
                      name="email"
                      type="text"
                      className={`peer placeholder-transparent h-10 w-full border-b-2 border-gray-300 text-gray-900 focus:outline-none focus:borer-rose-600 ${
                        validationErrors.email && "border-red-500"
                      }`}
                      placeholder="Email address"
                      onChange={handleChange}
                    />
                    <label
                      htmlFor="email"
                      className="absolute left-0 -top-3.5 text-gray-600 text-sm peer-placeholder-shown:text-base peer-placeholder-shown:text-gray-440 peer-placeholder-shown:top-2 transition-all peer-focus:-top-3.5 peer-focus:text-gray-600 peer-focus:text-sm"
                    >
                      Email Address
                    </label>
                    {validationErrors.email && (
                      <div className="text-red-500 text-sm">{validationErrors.email}</div>
                    )}
                  </div>
                  <div className="relative">
                    <input
                      id="password"
                      name="password"
                      type="password"
                      className={`peer placeholder-transparent h-10 w-full border-b-2 border-gray-300 text-gray-900 focus:outline-none focus:borer-rose-600 ${
                        validationErrors.password && "border-red-500"
                      }`}
                      placeholder="Password"
                      onChange={handleChange}
                    />
                    <label
                      htmlFor="password"
                      className="absolute left-0 -top-3.5 text-gray-600 text-sm peer-placeholder-shown:text-base peer-placeholder-shown:text-gray-440 peer-placeholder-shown:top-2 transition-all peer-focus:-top-3.5 peer-focus:text-gray-600 peer-focus:text-sm"
                    >
                      Password
                    </label>
                    {validationErrors.password && (
                      <div className="text-red-500 text-sm">{validationErrors.password}</div>
                    )}
                  </div>

                  <div className="relative">
                    <button
                      className="bg-blue-500 text-white rounded-md px-2 py-1"
                      onClick={handleLogin}
                    >
                      Submit
                    </button>
                  </div>
                  <div className=" bottom-5 w-full text-center text-gray-400 relative text-sm py-2">
                    Create a Account?
                    <Link to="/register" className="text-black">
                      Sign up
                    </Link>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </>
  );
};

export default Login;
