import React, { useEffect, useState } from "react";
import { useNavigate, useParams } from "react-router-dom";
import {
  createOrEditPostAsync,
  getPostByIDAsync,
  selectPost,
  selectPostError,
  selectPostStatus,
} from "../slices/post/postSlice";
import { useDispatch, useSelector } from "react-redux";

const EditPost = () => {
  const [preview, setPreview] = useState(null);
  const dispatch = useDispatch();
  const err = useSelector(selectPostError);
  const navigate = useNavigate();
  const status = useSelector(selectPostStatus);
  const post = useSelector(selectPost);
  const [error, setError] = useState(err);
  const [postData, setPostData] = useState({});
  // Get the post ID from URL params
  const { id: postId } = useParams();
  useEffect(() => {
    // Fetch post data if editing existing post
    if (postId) {
      dispatch(getPostByIDAsync(postId));
    }

    // Set preview image if post has thumbnail
  }, [postId]);

  useEffect(() => {
    setPostData(post);
    console.log(post);
    // Set preview image if post has thumbnail and it's not already a data URL
    // Set preview image if post has thumbnail
    if (post?.thumbnail) {
      setPreview(`/uploads/${post.thumbnail}`);
    }
  }, [post]);

  const handleChange = (e) => {
    const { name, value } = e.target;
    setPostData({ ...postData, [name]: value });
  };

  const handleImageChange = (e) => {
    const file = e.target.files[0];
    setPostData({ ...postData, thumbnail: file });

    // Preview the selected image
    const reader = new FileReader();
    reader.onload = () => {
      if (reader.readyState === 2) {
        setPreview(reader.result);
      }
    };
    reader.readAsDataURL(file);
  };

  const handleSubmit = (e) => {
    e.preventDefault();
    if (
      !postData.thumbnail ||
      !postData.category ||
      !postData.description ||
      !postData.title
    ) {
      setError({
        error: "Please fill in all fields",
      });
      setTimeout(() => {
        setError("");
      }, 3000);
    } else {
      dispatch(createOrEditPostAsync({ postId, ...postData }));
      navigate("/myposts");
      // You can handle form submission here, such as sending data to the server
    }
    console.log("Form submitted:", postData);
  };

  return (
    <>
      {error && (
        <div
          className="bg-red-100 border mt-5 border-red-400 text-red-700 px-4 py-3 rounded relative"
          role="alert"
        >
          <strong className="font-bold">Something went wrong!</strong>
          <span className="block sm:inline">{error.error}</span>
          <span
            className="absolute top-0 bottom-0 right-0 px-4 py-3 cursor-pointer"
            onClick={() => {
              setError("");
            }}
          >
            <svg
              className="fill-current h-6 w-6 text-red-500"
              xmlns="http://www.w3.org/2000/svg"
              viewBox="0 0 20 20"
            >
              <title>Close</title>
              <path
                fillRule="evenodd"
                d="M14.348 14.849a1.2 1.2 0 0 1-1.697 0L10 11.819l-2.651 3.029a1.2 1.2 0 1 1-1.697-1.697l2.758-3.15-2.759-3.152a1.2 1.2 0 1 1 1.697-1.697L10 8.183l2.651-3.031a1.2 1.2 0 1 1 1.697 1.697l-2.758 3.152 2.758 3.15a1.2 1.2 0 0 1 0 1.698z"
              />
            </svg>
          </span>
        </div>
      )}
      {status === "loading" ? (
        <h1>wait..</h1>
      ) : (
        <section className="max-w-4xl p-6 mx-auto bg-indigo-600 rounded-md shadow-md dark:bg-gray-800 mt-20">
          <h1 className="text-xl font-bold text-white capitalize dark:text-white">
            Create a new Post
          </h1>
          <form onSubmit={handleSubmit}>
            <div className="grid grid-cols-1 gap-6 mt-4 sm:grid-cols-2">
              <div>
                <label
                  className="text-white dark:text-gray-200"
                  htmlFor="title"
                >
                  Title
                </label>
                <input
                  id="title"
                  type="text"
                  name="title"
                  value={postData?.title}
                  onChange={handleChange}
                  className="block w-full px-4 py-2 mt-2 text-gray-700 bg-white border border-gray-300 rounded-md dark:bg-gray-800 dark:text-gray-300 dark:border-gray-600 focus:border-blue-500 dark:focus:border-blue-500 focus:outline-none focus:ring"
                />
              </div>

              <div>
                <label
                  className="text-white dark:text-gray-200"
                  htmlFor="category"
                >
                  Category
                </label>
                <select
                  id="category"
                  name="category"
                  value={postData?.category}
                  onChange={handleChange}
                  className="block w-full px-4 py-2 mt-2 text-gray-700 bg-white border border-gray-300 rounded-md dark:bg-gray-800 dark:text-gray-300 dark:border-gray-600 focus:border-blue-500 dark:focus:border-blue-500 focus:outline-none focus:ring"
                >
                  <option value="">Select a category</option>
                  <option value="Technology">Technology</option>
                  <option value="Entertainment">Entertainment</option>
                  <option value="Politics">Politics</option>
                  <option value="Science">Science</option>
                  <option value="Sports">Sports</option>
                  <option value="Travel">Travel</option>
                  <option value="Music">Music</option>
                  <option value="News">News</option>
                  <option value="Education">Education</option>
                  <option value="Weather">Weather</option>
                  <option value="Art">Art</option>
                  <option value="Business">Business</option>
                </select>
              </div>

              <div className="col-span-2">
                <label
                  className="text-white dark:text-gray-200"
                  htmlFor="description"
                >
                  Description
                </label>
                <textarea
                  id="description"
                  name="description"
                  value={postData?.description}
                  onChange={handleChange}
                  className="block w-full px-4 py-2 mt-2 text-gray-700 bg-white border border-gray-300 rounded-md dark:bg-gray-800 dark:text-gray-300 dark:border-gray-600 focus:border-blue-500 dark:focus:border-blue-500 focus:outline-none focus:ring"
                />
              </div>
              <div className="col-span-2">
                {/* File input div */}
                <div className="flex items-center">
                  <div className="flex-shrink-0">
                    <label
                      htmlFor="image-upload"
                      className="cursor-pointer bg-white rounded-md font-medium text-indigo-600 hover:text-indigo-500 focus-within:outline-none focus-within:ring-2 focus-within:ring-offset-2 focus-within:ring-indigo-500"
                    >
                      <span className="px-2 py-1">Upload a file</span>
                      <input
                        id="image-upload"
                        name="thumbnail"
                        type="file"
                        accept="image/*"
                        onChange={handleImageChange}
                        className="sr-only"
                      />
                    </label>
                  </div>
                  <div className="ml-4 ">
                    <p className="text-white">or drag and drop</p>
                    {/* Image preview */}
                    {preview && (
                      <div className="mt-2">
                        <img
                          src={preview}
                          alt="Preview"
                          className="max-w-full h-auto rounded-md"
                          style={{ width: "200px", height: "200px" }}
                        />
                      </div>
                    )}
                  </div>
                </div>
              </div>
            </div>
            <div className="flex justify-end mt-6">
              <button
                type="submit"
                className="px-6 py-2 leading-5 text-white transition-colors duration-200 transform bg-pink-500 rounded-md hover:bg-pink-700 focus:outline-none focus:bg-gray-600"
              >
                Create
              </button>
            </div>
          </form>
        </section>
      )}
    </>
  );
};

export default EditPost;
