import { createAsyncThunk, createSlice } from "@reduxjs/toolkit";
import {
  createOrEditPost,
  deletePostByPostID,
  getAllPosts,
  getAllPostsByCategory,
  getAllPostsByUser,
  getPostByID,
  getUserPosts,
} from "./postApi";

//set  initial state
const initialState = {
  posts: null,
  status: "idle",
  error: null,
  post: null,
};

export const getAllPostsAsync = createAsyncThunk(
  "post/getAllPosts",
  async () => {
    const response = await getAllPosts();
    // The value we return becomes the `fulfilled` action payload
    return response;
  }
);
export const getAllPostsByCategoryAsync = createAsyncThunk(
  "post/getAllPostsByCategory",
  async (category) => {
    const response = await getAllPostsByCategory(category);
    // The value we return becomes the `fulfilled` action payload
    return response.data;
  }
);
export const getAllPostsByUserAsync = createAsyncThunk(
  "post/getAllPostsByUser",
  async (UserID) => {
    const response = await getAllPostsByUser(UserID);
    // The value we return becomes the `fulfilled` action payload
    return response.data;
  }
);
export const getPostByIDAsync = createAsyncThunk(
  "post/getPostByID",
  async (PostID) => {
    const response = await getPostByID(PostID); // The value we return becomes the `fulfilled` action payload
    return response.data;
  }
);
export const getUserPostsAsync = createAsyncThunk(
  "post/getUserPosts",
  async () => {
    const response = await getUserPosts(); // The value we return becomes the `fulfilled` action payload
    return response.data;
  }
);
export const deletePostByPostIDAsync = createAsyncThunk(
  "post/deletePostByPostID",
  async (PostID) => {
    const response = await deletePostByPostID(PostID); // The value we return becomes the `fulfilled` action payload
    return response;
  }
);
export const createOrEditPostAsync = createAsyncThunk(
  "post/createOrEditPost",
  async (postData) => {
    const response = await createOrEditPost(postData); // The value we return becomes the `fulfilled` action payload

    return response.data;
  }
);
const postSlice = createSlice({
  name: "posts",
  initialState,
  reducers: {},
  extraReducers: (builder) => {
    builder
      .addCase(getAllPostsAsync.pending, (state) => {
        state.posts = null;
        state.status = "loading";
      })
      .addCase(getAllPostsAsync.fulfilled, (state, action) => {
        state.posts = action.payload;
        state.status = "success";
        state.post = null;
      })
      .addCase(getAllPostsAsync.rejected, (state, action) => {
        state.error = action.payload;
        state.status = "failed";
      })
      .addCase(getAllPostsByCategoryAsync.pending, (state) => {
        state.posts = null;
        state.status = "loading";
      })
      .addCase(getAllPostsByCategoryAsync.fulfilled, (state, action) => {
        state.posts = action.payload;
        state.status = "success";
        state.post = null;
      })
      .addCase(getAllPostsByCategoryAsync.rejected, (state, action) => {
        state.error = action.payload;
        state.status = "failed";
      })
      .addCase(getAllPostsByUserAsync.pending, (state) => {
        state.posts = null;
        state.status = "loading";
      })
      .addCase(getAllPostsByUserAsync.fulfilled, (state, action) => {
        state.posts = action.payload;
        state.status = "success";
        state.post = null;
      })
      .addCase(getAllPostsByUserAsync.rejected, (state, action) => {
        state.error = action.payload;
        state.status = "failed";
      })
      .addCase(getPostByIDAsync.pending, (state) => {
        state.post = null;
        state.status = "loading";
      })
      .addCase(getPostByIDAsync.fulfilled, (state, action) => {
        state.post = action.payload;
        state.status = "success";
      })
      .addCase(getPostByIDAsync.rejected, (state, action) => {
        state.error = action.payload;
        state.status = "failed";
      })
      .addCase(createOrEditPostAsync.pending, (state) => {
        state.status = "loading";
      })
      .addCase(createOrEditPostAsync.fulfilled, (state, action) => {
        state.status = "success";
      })
      .addCase(createOrEditPostAsync.rejected, (state, action) => {
        state.error = action.payload;
        state.status = "failed";
      })
      .addCase(getUserPostsAsync.pending, (state) => {
        state.posts = null;
        state.status = "loading";
      })
      .addCase(getUserPostsAsync.fulfilled, (state, action) => {
        state.posts = action.payload;
        state.status = "success";
      })
      .addCase(getUserPostsAsync.rejected, (state, action) => {
        state.error = action.payload;
        state.status = "failed";
      })
      .addCase(deletePostByPostIDAsync.pending, (state) => {
        state.status = "loading";
      })
      .addCase(deletePostByPostIDAsync.fulfilled, (state, action) => {
        state.status = "success";
      })
      .addCase(deletePostByPostIDAsync.rejected, (state, action) => {
        state.error = action.payload;
        state.status = "failed";
      });
  },
});

export const selectAllPosts = (state) => state.post.posts;
export const selectPost = (state) => state.post.post;
export const selectPostStatus = (state) => state.post.status;
export const selectPostError = (state) => state.post.error;

export default postSlice.reducer;
