export function deletePostByPostID(PostID) {
  return new Promise(async (resolve) => {
    const Response = await fetch(`/api/posts/${PostID}`, {
      method: "DELETE",
      // credentials: "include",
    });
    const data = await Response.json();
    resolve({ data });
  });
}

export function createOrEditPost({
  title,
  category,
  description,
  thumbnail,
  postId,
}) {
  return new Promise(async (resolve) => {
    const formData = new FormData();
    formData.append("title", title);
    formData.append("category", category);
    formData.append("description", description);
    formData.append("thumbnail", thumbnail);

    const requestOptions = {
      method: postId ? "PATCH" : "POST", // Use PUT method if postId is provided for editing an existing post
      body: formData,
    };

    const url = postId ? `/api/posts/${postId}` : "/api/posts"; // Use different endpoint for editing existing post

    const response = await fetch(url, requestOptions);

    const data = await response.json();
    resolve({ data });
  });
}
