import React, { useEffect, useState } from "react";
import { Link, useParams } from "react-router-dom";
import { data } from "../dummy_data";
import { useDispatch, useSelector } from "react-redux";
import {
  getAllPostsByCategoryAsync,
  selectAllPosts,
} from "../slices/post/postSlice";

const CategoryPost = () => {
  const params = useParams();
  const dispatch = useDispatch();
  const posts = useSelector(selectAllPosts);
  useEffect(() => {
    dispatch(getAllPostsByCategoryAsync(params.category));
  }, [dispatch, params]);
  const articles =
    posts && posts.length > 0
      ? posts.map((article) => {
          // Convert createdAt timestamp to a human-readable date
          const createdAtDate = new Date(article.createdAt);
          const formattedCreatedAt = createdAtDate.toLocaleDateString("en-US", {
            year: "numeric",
            month: "long",
            day: "numeric",
          });

          const short_discription =
            article.description.length > 100
              ? article.description.substr(0, 100) + "..."
              : article.description;
          return (
            <>
              {posts ? (
                <article className="overflow-hidden rounded-lg shadow transition hover:shadow-lg dark:shadow-gray-700/25 w-72 ">
                  <img
                    alt="Office"
                    src={`http://localhost:8080/uploads/${article.thumbnail}`}
                    className="h-56 w-full object-cover"
                  />
                  <div className="bg-white p-4 h-full dark:bg-gray-900 sm:p-6">
                    <time
                      dateTime={article.createdAt}
                      className="block text-xs text-gray-500 dark:text-gray-400"
                    >
                      {formattedCreatedAt}
                    </time>
                    <Link to={`/posts/${article._id}`}>
                      <h3 className="mt-0.5 text-lg text-gray-900 dark:text-white">
                        {article.title}
                      </h3>
                    </Link>

                    <p className="mt-2 line-clamp-3 text-sm/relaxed text-gray-500 dark:text-gray-400">
                      {short_discription}
                    </p>

                    <div className="flex items-center mt-3 space-x-12 flex-row">
                      <Link to={`/post/users/${article.creator._id}`}>
                        <div className="text-sm flex">
                          <img
                            className="w-10 h-10 rounded-full mr-4"
                            src={`http://localhost:8080/uploads/${article.creator.avatar}`}
                            alt="Avatar of Writer"
                          />
                          <div>
                            <p className=" text-amber-100 leading-none text-sm ">
                              {article.creator.name}
                            </p>
                            {/* <p className="text-gray-600">Aug 9</p>   */}
                          </div>
                        </div>
                      </Link>
                      <Link to={`/posts/categories/${article.category}`}>
                        <div className="category_type text-amber-50 text-sm">
                          <h5>{article.category}</h5>
                        </div>
                      </Link>
                    </div>
                  </div>
                </article>
              ) : (
                <h2>no posts found</h2>
              )}
            </>
          );
        })
      : null;
  return (
    <div className="container">
      {articles ? (
        articles
      ) : posts?.length === 0 ? (
        <h1>no posts found..</h1>
      ) : (
        <h1>wait...</h1>
      )}
    </div>
  );
};

export default CategoryPost;
