import React, { useEffect, useState } from "react";
import { Link, Navigate, useNavigate } from "react-router-dom";
import { useDispatch, useSelector } from "react-redux";
import {
  deletePostByPostIDAsync,
  getUserPostsAsync,
  selectAllPosts,
} from "../slices/post/postSlice";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faEdit, faTrash } from "@fortawesome/free-solid-svg-icons";

const UserPosts = () => {
  const navigate = useNavigate();
  const dispatch = useDispatch();
  const posts = useSelector(selectAllPosts);
  const [postState, setPostState] = useState([]);

  useEffect(() => {
    dispatch(getUserPostsAsync());
  }, []);
  useEffect(() => {
    setPostState(posts);
  }, [posts]);

  const articles =
    postState && postState.length > 0
      ? postState.map((article, index) => {
          // Convert createdAt timestamp to a human-readable date
          const createdAtDate = new Date(article.createdAt);
          const formattedCreatedAt = createdAtDate.toLocaleDateString("en-US", {
            year: "numeric",
            month: "long",
            day: "numeric",
          });

          const short_discription =
            article.description.length > 100
              ? article.description.substr(0, 100) + "..."
              : article.description;
          return (
            <article className="overflow-hidden rounded-lg shadow transition hover:shadow-lg dark:shadow-gray-700/25 w-72 ">
              <img
                alt="Office"
                src={`http://localhost:8080/uploads/${article.thumbnail}`}
                className="h-56 w-full object-cover"
              />
              <div className="bg-white p-4 h-full dark:bg-gray-900 sm:p-6">
                <time
                  dateTime={article.createdAt}
                  className="block text-xs text-gray-500 dark:text-gray-400"
                >
                  {formattedCreatedAt}
                </time>
                <Link to={`/posts/${article._id}`}>
                  <h3 className="mt-0.5 text-lg text-gray-900 dark:text-white">
                    {article.title}
                  </h3>
                </Link>

                <p className="mt-2 h-10 line-clamp-3 text-sm/relaxed text-gray-500 dark:text-gray-400">
                  {short_discription}
                </p>
                <div className="flex justify-between mt-4">
                  <Link to={"/posts/edit/" + article._id}>
                    <button className="rounded-md bg-blue-500 text-white px-4 py-2 hover:bg-blue-600">
                      <FontAwesomeIcon icon={faEdit} />
                    </button>
                  </Link>
                  <button
                    className="rounded-md bg-red-500 text-white px-4 py-2 hover:bg-red-600"
                    onClick={() => {
                      dispatch(deletePostByPostIDAsync(article._id));
                      const updatedPosts = postState.filter(
                        (_, i) => i !== index
                      );
                      setPostState(updatedPosts);
                    }}
                  >
                    <FontAwesomeIcon icon={faTrash} />
                  </button>
                </div>
              </div>
            </article>
          );
        })
      : null;
  return <div className="container">{articles && articles}</div>;
};

export default UserPosts;
