import { createAsyncThunk, createSlice } from "@reduxjs/toolkit";
import { checkAuth, createUser, logOut, loginUser } from "./authApi";
import Cookies from "js-cookie";
const setuserdata = () => {
  const user = Cookies.get("jwt_token");
  if (user) {
    return JSON.parse(localStorage.getItem("userInfo"));
  } else {
    return null;
  }
};
const initialState = {
  loggedInUser: setuserdata(),
  status: "idle",
  error: null,
};

export const createUserAsync = createAsyncThunk(
  "auth/createUser",
  async (userData) => {
    const response = await createUser(userData);
    // The value we return becomes the `fulfilled` action payload
    return response.data;
  }
);
export const loginUserAsync = createAsyncThunk(
  "auth/loginUser",
  async (userData) => {
    const response = await loginUser(userData);
    // The value we return becomes the `fulfilled` action payload
    return response.data;
  }
);
export const logOutAsync = createAsyncThunk("auth/logOut", async () => {
  const response = await logOut();
  // The value we return becomes the `fulfilled` action payload
  return response.data;
});

// export const checkAuthAsync = createAsyncThunk("auth/checkAuth", async () => {
//   try {
//     const response = await checkAuth();
//     console.log("response" + response);
//     // The value we return becomes the `fulfilled` action payload
//     return response.data;
//   } catch (error) {}
// });
const authSlice = createSlice({
  name: "auth",
  initialState,
  reducers: {},
  extraReducers: (builder) => {
    builder
      .addCase(createUserAsync.pending, (state) => {
        state.status = "loading";
      })
      .addCase(createUserAsync.fulfilled, (state, action) => {
        state.status = "idle";
        state.loggedInUser = null;
      })
      .addCase(createUserAsync.rejected, (state, action) => {
        state.status = "done";
        state.error = action.payload;
      })
      .addCase(logOutAsync.pending, (state) => {
        state.status = "loading";
      })
      .addCase(logOutAsync.fulfilled, (state, action) => {
        state.status = "idle";
        state.loggedInUser = null;
        localStorage.removeItem("userInfo");
        Cookies.remove("jwt_token");
      })
      .addCase(loginUserAsync.rejected, (state, action) => {
        state.status = "idle";
        state.error = action.payload;
      })
      .addCase(loginUserAsync.pending, (state) => {
        state.status = "loading";
      })
      .addCase(loginUserAsync.fulfilled, (state, action) => {
        state.status = "idle";
        state.loggedInUser = {
          name: action.payload.name,
          email: action.payload.email,
          avatar: action.payload.avatar,
        };
        Cookies.set("jwt_token", action.payload.token);
        localStorage.setItem(
          "userInfo",
          JSON.stringify({
            name: action.payload.name,
            email: action.payload.email,
            avatar: action.payload.avatar,
          })
        );
      });
    // .addCase(checkAuthAsync.pending, (state) => {
    //   state.status = "loading";
    // })
    // .addCase(checkAuthAsync.fulfilled, (state, action) => {
    //   state.status = "idle";
    //   state.loggedInUser = action.payload;
    // })
    // .addCase(checkAuthAsync.rejected, (state, action) => {
    //   state.status = "idle";
    // });
  },
});

export const selectLoggedInUser = (state) => state.auth.loggedInUser;
export const selectError = (state) => state.auth.error;
export const { setUserInfo, logout } = authSlice.actions;

export default authSlice.reducer;
